@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

input[type="submit"], button { cursor: pointer; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-sepa:before { content: ""; }

.icon-skrill:before { content: ""; }

.icon-rus { background-image: url("../img/icons.png"); width: 45px; height: 30px; background-position: -273px -237px; }

.icon-kaz { background-image: url("../img/icons.png"); width: 45px; height: 30px; background-position: -126px -237px; }

.icon-arm { background-image: url("../img/icons.png"); width: 45px; height: 30px; background-position: -224px -237px; }

.icon-gbr { background-image: url("../img/icons.png"); width: 45px; height: 30px; background-position: -175px -237px; }

.icon-zaf { background-image: url("../img/icons.png"); width: 45px; height: 30px; background-position: -126px -271px; }

.icon-skill { background-image: url("../img/icons.png"); width: 78px; height: 78px; background-position: -166px -153px; }

.icon-reputation { background-image: url("../img/icons.png"); width: 80px; height: 80px; background-position: 0px -153px; }

.icon-productions { background-image: url("../img/icons.png"); width: 78px; height: 78px; background-position: -264px -82px; }

.icon-experience { background-image: url("../img/icons.png"); width: 78px; height: 78px; background-position: 0px -237px; }

.icon-equipment { background-image: url("../img/icons.png"); width: 78px; height: 78px; background-position: -264px 0px; }

.icon-development { background-image: url("../img/icons.png"); width: 78px; height: 78px; background-position: -84px -153px; }

.icon-car { background-image: url("../img/icons.png"); width: 58px; height: 30px; background-position: -197px -113px; }

.btn { width: 260px; height: 40px; line-height: 40px; background-color: #ccc; color: #555; font-family: Arial; font-size: 18px; font-weight: 700; text-decoration: none; display: inline-block; text-align: center; position: relative; cursor: pointer; }
.btn.is-active { background-color: #fe360a; color: #fff; }

.container { *zoom: 1; }
.container:after { content: " "; display: table; clear: both; }

.container_pad { margin: 0 0 0 20px; }
.container_pad [class*='col-']:last-of-type { padding-right: 20px; }

[class*="col"] { float: left; padding-right: 20px; box-sizing: border-box; }
[class*="col"]:last-of-type { padding-right: 0; }

.col1 { width: 25%; }

.col2 { width: 50%; }

.col3 { width: 75%; }

.col4 { width: 100%; }

body { font-family: Arial, sans-serif; line-height: 1.2; color: black; font-size: 16px; font-weight: 400; background-color: #FFF; }

h1, h2, h3, h4 { color: black; font-family: Arial; font-size: 48px; font-weight: 700; line-height: 1; }
h1.is-uppercase, h2.is-uppercase, h3.is-uppercase, h4.is-uppercase { text-transform: uppercase; }

h1 { margin-bottom: 30px; }

h2 { font-size: 32px; }

h3 { font-size: 24px; margin-bottom: 20px; }

h4 { font-size: 20px; }

p { margin-bottom: 22px; color: #000; font-family: Arial; font-size: 16px; font-weight: 400; }

a { color: #006cd9; font-family: Arial; font-size: 16px; font-weight: 400; text-decoration: underline; }
a:hover { text-decoration: none; }

hr { height: 5px; background-color: #666; display: block; width: 100%; border: 0; outline: 0; margin: 0; }

.out { width: 1140px; margin: auto; padding: 28px 20px; }

.header__top-line { margin-bottom: 30px; }

.header__logo { font: 0/0 a; text-shadow: none; color: transparent; background-image: url("../img/icons.png"); width: 260px; height: 109px; background-position: 0px 0px; display: inline-block; vertical-align: middle; }

.header__text { font-style: italic; font-family: Arial; font-size: 16px; display: inline-block; vertical-align: middle; padding: 0 83px 0 20px; }

.header__tel { font-family: Arial; font-size: 24px; font-weight: 400; display: inline-block; vertical-align: middle; }

.navigation { height: 40px; background-color: #f4ecea; background-image: linear-gradient(to top, #ff2e06 0%, #f89232 100%); text-align: center; }
.navigation a { display: inline-block; color: white; font-family: Arial; font-size: 22px; font-weight: 700; line-height: 40px; text-decoration: none; padding: 0 35px; transition: all 0.3s; }
.navigation a:hover, .navigation a a.is-active { background-color: #000; background-image: linear-gradient(to top, #000 0%, #555 100%); }

.select { position: relative; background-color: white; border: 2px solid #f1f1f1; border-radius: 10px; padding: 10px 10px 0; max-width: 230px; width: 100%; display: inline-block; vertical-align: middle; cursor: pointer; }
.select:after { content: " "; display: block; position: absolute; z-index: 1; background-image: url("../img/icons.png"); width: 14px; height: 8px; background-position: -346px -206px; right: 25px; top: 23px; }
.select li { display: none; font-family: Arial; font-size: 16px; font-weight: 400; color: #000; margin-bottom: 10px; }
.select li.is-dropdown { display: block; }
.select li > li { display: none; }
.select li i { vertical-align: middle; margin-right: 11px; }
.select li span { display: inline-block; position: relative; top: 1px; }

.advantage__top { position: relative; text-align: center; font-size: 0; }
.advantage__top h2 { margin-bottom: 31px; }
.advantage__top .btn { margin: 0 10px 30px; }

.advantage__text { display: inline-block; vertical-align: middle; }

.advantage__list { *zoom: 1; }
.advantage__list:after { content: " "; display: table; clear: both; }
.advantage__list article { margin-bottom: 20px; }
.advantage__list p { margin-bottom: 0; }
.advantage__list h3 { margin-bottom: 7px; }
.advantage__list i { display: inline-block; vertical-align: middle; margin-right: 22px; }

.advantage__unit { display: none; }
.advantage__unit.is-active { display: block; }


